


.osahan-navbar-search {
  position: relative;
}
.boxsearch {
  position: absolute;
  top: 100%; 
  left: 0; 
  width: 100%; 
  max-height: 300px; 
  overflow-y: auto; 
  background: white;
  border: 1px solid #ddd;
  z-index: 1000; 
  border-radius: 4px;
}
.boxsearch div {
  padding: 10px;
  border-bottom: 1px solid #ddd;
}
.boxsearch a:hover {
  background-color: #f1f1f1; 
}
.hide{
  opacity: 0;
  visibility: hidden;
  position: absolute;}
